// Import node_modules
import React from 'react';
import PropTypes from 'prop-types';
import { Provider } from 'react-redux';
import { Router, Route, Switch } from 'react-router';

// Import local source files.
import { Component } from 'tp-ui';
import MainView from 'components/MainView';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';

const favicon = require('tp-app/resources/favicon.ico');

/**
 * Typedef for React Components and Elements
 * @typedef {Object} HTML - The React Component
 */

/**
 * The GD application routes.
 */
class App extends Component {
  /**
   * Constructor for the top-level GD application component.
   *
   * @param {Object} props - The properties of the component.
   * @param {Object} props.history - the history object for react router
   */
  constructor(props) {
    super(props);
  }

  /**
   * Render function for the APP component.
   * @return {HTML} The JSX component.
   */
  render() {
    const { store, history } = this.props;

    const theme = createMuiTheme({
      palette: {
        primary: {
          light: '#e4ffff',
          main: '#b1ede8',
          dark: '#80bab6',
          contrastText:'#000000',
        },
        secondary: {
          light: '#c25646',
          main: '#8c271e',
          dark: '#590000',
          contrastText: '#ffffff',
        },
      },
      typography: {
        useNextVariants: true,
        fontFamily: [
          'Dense',
        ]
      }
    });
    return (
      <MuiThemeProvider theme={theme}>
        <Provider store={store}>
          <Router history={history}>
            <Switch>
              <Route path='*' component={MainView} />
            </Switch>
          </Router>
        </Provider>
      </MuiThemeProvider>
    );
  }
}

App.propTypes = {
  history: PropTypes.object.isRequired,
  store: PropTypes.object.isRequired,
};

export default App;
