// Import node_modules
import React from 'react';
import PropTypes from 'prop-types';

/**
 * Implementation of React.Component that autobinds all methods  of the class.
 * Should be used a base class for all GD React components that have state.
 */
export class Component extends React.Component {
  /**
   * Component constructor
   * @param {Object} props - The component properties
   * @param {Function} props.onLoadData - Callback to load the data for this component.
   * @param {Function} props.onUnloadData - Callback to unload the data for this component.
   * @param {Boolean} props.shouldLoadData - True, if the data needs loaded.
   * @param {Object} context - The component context
   * @return {void}
   */
  constructor(props, context) {
    super(props, context);

    this.autoBind();
  }

  /**
   * Bind the methods to this.
   * @param {Function[]} methods - List of function to bind
   * @return {void}
   */
  bind(methods) {
    methods.forEach(method => {
      this[method] = this[method].bind(this);
    });
  }

  /**
   * Bind all methods of this class to this.
   * @return {void}
   */
  autoBind() {
    this.bind(
      Object.getOwnPropertyNames(this.constructor.prototype)
        .filter(prop => typeof this[prop] === 'function'),
    );
  }

  /**
   * Load data if needed when the component is mounted.
   * @return {void}
   */
  componentDidMount() {
    const { onLoadData, shouldLoadData } = this.props;

    if(onLoadData && shouldLoadData) {
      // A data loading callback was supplied and the data is
      // not marked as initialized.
      onLoadData();
    }
  }

  /**
   * React Lifecycle Event.
   * Load data if needed when the component is updated.
   * @return {void}
   */
  componentDidUpdate() {
    const { onLoadData, shouldLoadData } = this.props;

    if(onLoadData && shouldLoadData) {
      // A data loading callback was supplied and the data is
      // not marked as initialized.
      onLoadData();
    }
  }

  /**
   * Unload data when the component is unmounting.
   * @return {void}
   */
  componentWillUnmount() {
    const { onUnloadData } = this.props;

    if(onUnloadData) {
      // A unload callback was supplied, invoke the callback.
      onUnloadData();
    }
  }
}

Component.defaultProps = {
  onLoadData: undefined,
  onUnloadData: undefined,
  shouldLoadData: false,
};

Component.propTypes = {
  onLoadData: PropTypes.func,
  onUnloadData: PropTypes.func,
  shouldLoadData: PropTypes.bool,
};

export default Component;
