// Import node_modules
import React from 'react';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { Switch, Route } from 'react-router';

// Import package modules
import { Component } from 'tp-ui';
import HomePage from 'components/HomePage'

// Import styles
import './MainView.scss';

const logo = require('tp-app/resources/logo-transparent.png')

/**
 * The applications main view
 */
class MainView extends Component {
  /**
   * Constructor for the MainView component
   *
   * @param {Object} props - The properties of the component.
   */
  constructor(props) {
    super(props);

    this.state = {
    };
  }

  /**
   * Render the component
   *
   * @return {Object} The React element
   */
  render() {
    return (
      <div className='main-view'>
        <AppBar className='main-appbar' position='sticky'>
          <Toolbar>
            <img className='nav-logo' src={logo}/>
            <Typography variant="h4">
              TimberPix
            </Typography>
          </Toolbar>
        </AppBar>
        <Switch>
          <Route path='*' component={HomePage} />
        </Switch>
      </div>
    );
  }
}

MainView.defaultProps = {
};

MainView.propTypes = {
};

export default MainView;
