// Import node_modules
import React from 'react';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';
import TextField from '@material-ui/core/TextField';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';

// Import styles
import './HomePage.scss';

/**
 * The Home Page
 *
 * @param {Object} props - The properties of the component.
 *
 * @return {Object} The React Component.
 */
const HomePage = ({}) => {
  const bannerImage = require('tp-app/resources/banner.jpg')
  const quadPic = require('tp-app/resources/quad-pic.png')

  return (
    <div className='home-page'>
      <img src={bannerImage} className='home-banner'/>
      <div className='info-panel-1'>
        <img src={quadPic} className='home-quad-pic'/>
        <div className='info-1-text'>
          <Typography variant="h2">
            Sentimental Gifts of Handcrafted Perfection
          </Typography>
          <br/>
          <br/>
          <Typography variant="h4">
            At Timber Pix we offer a wide range of both custom and original products and designs.
            Check it out now!
          </Typography>
          <div className='info-1-buttons'>
            <Button onClick={() => document.location.href="https://www.etsy.com/shop/TimberPixDesign"} className='info-button' variant="outlined" color='secondary' size='large'> View Our Selection </Button>
          </div>

        </div>
      </div>
      <Divider variant='middle' className='home-divider' color='secondary'></Divider>
      <div className='info-panel-2'>
        <Typography variant='h3'>
          Subscribe to our Newsletter for 20% off!
        </Typography>
        <form action="https://etsy.us20.list-manage.com/subscribe/post?u=7655de8b0fd8d4845cabac0a8&amp;id=a3b20e0912" method="post" id="mc-embedded-subscribe-form" name="mc-embedded-subscribe-form" className="validate" target="_blank">
          <TextField className='sub-text' required variant='outlined' name='FNAME' type='string' label='First Name'>FirstName</TextField>
          <TextField className='sub-text' required variant='outlined' name='EMAIL' type='email' label='Email Address'>EmailAddress</TextField>
          <Typography className='legal'>
            Timber Pix Design will use the information you provide on this form to be in touch with you and to provide updates, emails, giveaways, newsletters, and marketing.
          </Typography>
          <FormControlLabel 
            control={<Checkbox className='sub-check' color='primary' inputProps={{ 'aria-label': 'I Agree' } } required/>}
            label='I Agree'/>
          <Typography className='legal'>
          You can unsubscribe at any time by clicking the link in the footer of our emails.
          <br/>
          We use Mailchimp as our marketing platform. By clicking below to subscribe, you acknowledge that your information will be transferred to Mailchimp for processing. <a href='https://mailchimp.com/legal'>Learn more about Mailchimp's privacy practices here.</a>
          </Typography>
          <Button className='subscribe info-button' variant="contained" color='primary' size='large' type='submit'>Subscribe</Button>
        </form>
      </div>
    </div>
  );
};

HomePage.defaultProps = {
};

HomePage.propTypes = {
};

export default HomePage;
