//
// ACTION TYPES
//

export const INCREMENT_COUNTER = 'INCREMENT_COUNTER';

/**
 * Action to increment the counter
 *
 * @return {Object} the action
 */
export function incrementCounter() {
  return {
    type: INCREMENT_COUNTER,
  };
}
