// Import polyfills
import 'es6-object-assign/auto';

// Import node_modules
import React from 'react';
import ReactDOM from 'react-dom';
import { createStore } from 'redux';
import { AppContainer } from 'react-hot-loader';
import { createBrowserHistory } from 'history';

// Import local source files.
import 'index.html';
import App from 'components/App';
import { reducers } from 'tp-store';

// Import styles.
import 'app.scss';

const history = createBrowserHistory();

// Create the Redux store for the GD application
const store = createStore(reducers);

// Render the application.
// Disable React Hot Loader Warnings because they're annoying and not helpful
ReactDOM.render((
  <AppContainer warnings={false}>
    <App store={store} history={history} />
  </AppContainer>
), document.getElementById('app'));

// Hot Module Replacement API
if(module.hot) {
  module.hot.accept('./components/App.jsx', () => {
    const NewApp = require('./components/App.jsx').default;
    ReactDOM.render(
      <AppContainer warnings={false}>
        <NewApp store={store} history={history} />
      </AppContainer>,
      document.getElementById('app'),
    );
  });
}
